  doRefresh(refresher) {
    //下拉刷新

    this.addObjTxt(this.toUserId);

    if (this.addUserRatingEnd == false) {
      //没有更多评论 终止加载

      refresher.complete();
      return;
    }

    setTimeout(() => {

      refresher.complete();
    }, 1000);
  }

